import React from 'react';

// Utilities 
import utilityService from '../../../services/utils/utilityServices';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import { get } from '../../../redux/async/transactionsAsync';

// MUI Components
import { DataGrid } from '@mui/x-data-grid';

// Page Components
import GridLoadingOverlay from '../grid/GridLoadingOverlay';
import GridNoRowOverlay from '../grid/GridNoRowOverlay';
import { format, parseISO } from 'date-fns';

const LoadingOverlayOverride = () => {
    return (
        <GridLoadingOverlay />
    );
}

const NoRowOverlayOverride = () => {
    return (
        <GridNoRowOverlay />
    );
}



export default function MembersTransactions(props) {

    const dispatch = useDispatch();
    const transactionsRedux = useSelector((state) => state.transactions.value);
    const loading = transactionsRedux.loading;
    const [rowData, setRowData] = React.useState([]);

    const [selected, setSelected] = React.useState([]);
    const [isMultipleDelete, setIsMultipleDelete] = React.useState(true);

    const width = 170;

    const columns = [
        {
            field: 'account_number', headerName: 'Account Number', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.account_number === null ? "..." : params.row.account_number}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'account_type', headerName: 'Account Type', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.account_type === null ? "..." : utilityService.capitalizeFirstLetter(params.row.account_type)}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'activity', headerName: 'Transaction', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.activity === null ? "..." : params.row.activity}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'created_at', headerName: 'Transaction Date', flex: 1, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.created_at === null ? "..." : format(parseISO(params.row.created_at), "MMMM dd, yyyy h:mm:ss a")}
                        </span>
                    </div>
                );
            }
        },
        {
            field: 'amount', headerName: 'Amount', width: width, renderCell: (params) => {
                return (
                    <div>
                        <span>
                            {params.row.amount === null ? "..." : parseFloat(params.row.amount).toFixed(2)}
                        </span>
                    </div>
                );
            }
        },
    ];

    const handleSelection = (value) => {

        setSelected(value);

        if (value.length > 0) {
            setIsMultipleDelete(false);
        } else {
            setIsMultipleDelete(true);
        }
    }

    const search = (text) => {
        let filtered = utilityService.filterArrayOfObjects(text, transactionsRedux.transactions);
        setRowData(filtered);
    };

    const loadTransactions = React.useCallback(() => {
        dispatch(get());
    }, [dispatch])

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            loadTransactions();
        }

        return () => {
            mounted = true;
        }

    }, [loadTransactions]);

    return (
        <div style={{ height: '100%', width: '100%' }}>
            <DataGrid
                rows={rowData.length <= 0 ? transactionsRedux.transactions : rowData}
                columns={columns}
                selectionModel={selected}
                onSelectionModelChange={handleSelection}
                loading={loading}
                density="standard"
                disableSelectionOnClick
                disableColumnMenu
                components={{
                    LoadingOverlay: LoadingOverlayOverride,
                    NoRowsOverlay: NoRowOverlayOverride,
                }}
                componentsProps={{
                    toolbar: {
                        isMultipleDelete: isMultipleDelete,
                        onChange: (event) => search(event.target.value),
                    },
                }}
            />
        </div>
    );
}